@import './_reset.scss';

@mixin horizental_flex() {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

$text_shadow: 1px 1px 2px #000;

@mixin  vertical_flex() {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.header {
    width: 100%;
    margin: 0 0 100px 0;
    position: fixed;
    .header_in {
        @include horizental_flex();
        background: rgba(255, 255, 255, .3);
        .logo {
            height: 100px;
            flex: 1;
            text-align: center;
            line-height: 100px;
            a {
                color: #fff;
                text-shadow: $text_shadow;
                font-size: 1.2rem;
                font-weight: bold;
            }
        }
        .menu {
        @include horizental_flex();
        height: 100px;
        flex: 5;

        ul {
        @include horizental_flex();
        width: 100%;
        justify-content: space-evenly;

        a {
            color: #fff;
            text-shadow: $text_shadow;
            font-size: 1.2rem;
            font-weight: bold;
        }

        }

        }
        .info_menu_wrap {
        @include vertical_flex();
        justify-content: space-evenly;
        height: 100px;
        flex: 2;

        .info_menu {
            width: 100%;
        }

        ul {
            
            @include horizental_flex();

            justify-content: space-evenly;
            a {
                color: #fff;
                font-size: .9rem;
            }

        }

        .search_box {

            position: relative;

            &::before {
                content: '';
                display: block;
                position: absolute;
                width: 20px;
                height: 20px;
                background: url(../search.png) no-repeat 50% 0 / 100%;
                top: 50%;
                transform: translateY(-50%);
                right: 30px;
            }
        }

        input {
            width: 300px;
            height: 30px;
            outline: none;
            border: none;
            padding: 0 0px 0 10px;
            border-radius: 8px;
        }

        }
    }
}

@media (max-width: 1920px) and (min-width: 1200px) {
    .header {
        .header_in {
            .logo {
                flex: 1;
                a {
                    font-size: .8rem;
                    font-weight: bold;
                }
            }
            .info_menu_wrap {
        
            ul {
                
                a {
                    font-size: .7rem;
                }
    
            }
            .search_box {

                position: relative;
    
                &::before {
                    right: 15px;
                }
            }
    
        
            input {
                width: 200px;
                font-size: .8rem;
                
            }
    
            }
        }
    }

}

@media (max-width: 1199px) and (min-width: 769px) {
    .header {
        .header_in {
            .logo {
                flex: 1;
            }
            .menu, .info_menu_wrap {
                display: none;
            }

            }
        }

    
    }
    

@media (max-width: 768px) and (min-width: 320px){
    .header {
        .header_in {
            .logo {
                flex: 1;
            }
            .menu, .info_menu_wrap {
                display: none;
            }

            }
        }

    }
    


                  